import React, { Component } from 'react'
import BlueTitle from '../../component/BlueTitle'
import Contact from '../../component/Contact'
import {Collapse, Tag} from 'antd'
import './index.css'
import QueueAnim from 'rc-queue-anim'

const {Panel} = Collapse;

const panelset = [{
    header:'UI ',
    number:2,
    content:[{
        title:"",
        body:['','']
    }]
},{
    header:'iOS',
    number:2,
    content:[{
        title:"",
        body:['','']
    }]
},{
    header:'Android',
    number:2,
    content:[{
        title:"",
        body:['','']
    }]
},{
    header:'Web',
    number:2,
    content:[{
        title:"",
        body:['web','','','']
    },{
        title:"",
        body:['HTML/XHTMLCSS',
        '',
        'HTML5HTML5',
        'JavaScriptAjaxWeb, AngularJSVUE.JS']
    }]
}]

export default class Join extends Component {
    render() {
        return (
            <div>
                <BlueTitle content='' className='join-title'/>
                {this.renderP1()}
                <Contact />
            </div>
        )
    }
    renderP1(){
        return (
            <QueueAnim type='bottom' duration='1000'>
            <div className='white join' key='1'>
                <h2></h2>
                <Collapse expandIconPosition='right' bordered={false} defaultActiveKey="3">
                    {
                        panelset.map((item,index) => (
                            <Panel header={item.header} key={index} extra={this.renderTag(item.number)} className='item-header'>
                                {
                                    item.content.map((con,i)=>(
                                        <div key={i}>
                                            <p className='item-title'>{con.title}</p>
                                            <ul className='item-con'>
                                                {
                                                    con.body.map((li,j)=>
                                                    <li key={j}>{li}</li>
                                                    )
                                                }
                                            </ul>
                                        </div>
                                    ))
                                }
                                <div className='remind'>
                                    <p><b>ceo@quseit.com</b></p>
                                    <p>:<b>+</b></p>
                                </div>
                            </Panel>
                       ))
                    }
                    
                </Collapse>
                
            </div>
            </QueueAnim>
        )
    }
    renderTag(text){
        return (
            <Tag className='header-tag'>{text}</Tag>
        )
    }
}
