import React, { Component } from 'react'
import Contact from '../../component/Contact'
import {Input, Row, Col, Checkbox, Button, } from 'antd'
import './index.css'
import Texty from 'rc-texty'
import QueueAnim from 'rc-queue-anim'

const {TextArea} = Input;
export default class Cooperate extends Component {
    constructor(props){
        super(props)
        this.state = {
            name:'',
            email:'',
            contact:'',
            projectName:'',
            money:'',
            date:'',
            projectType:'',
            requireMents:''
        };
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleName = this.handleName.bind(this);
        this.handleCheckbox = this.handleCheckbox.bind(this);
    }
    render() {
        return (
            <div className='cooperate'>
                <Row className='cooperate-title blue'>
                        <Texty className='t1' duration="600"> |</Texty>
                        <Texty className='t2' duration='800'></Texty>
                </Row>
                <QueueAnim type='bottom'>
                    <div key='1'>
                         <div className='white cooperate-p1'>
                     
                    <b>ceo@quseit.com</b>
                    1-3
                    </div>
                
                    {this.renderForm()}
                    </div>
                   
                </QueueAnim>
                

                <Contact/>
                
            </div>
        )
    }
    renderForm(){
        return (
            <form className='white cooperate-form' onSubmit={this.handleSubmit}>
                <div>
                    <p></p>
                    <Row>
                        <Col md={8} className='input-item'>
                            <label>*</label>
                            <Input value={this.state.name} onChange={this.handleName} allowClear size='large' placeholder='' className='input'/>
                        </Col>
                        <Col md={8} className='input-item'>
                            <label>*</label>
                            <Input value={this.state.email} onChange={(e)=>this.setState({email:e.target.value})} allowClear size='large' placeholder='Email' className='input'/>
                        </Col>
                        <Col md={8} className='input-item'>
                            <label>/</label>
                            <Input value={this.state.contact} onChange={(e)=>this.setState({contact:e.target.value})} allowClear size='large' placeholder='' className='input'/>
                        </Col>
                    </Row>
                </div>
                <div className='form-item'>
                    <p></p>
                    <Row>
                        <Col md={8} className='input-item'>
                            <label>/</label>
                            <Input value={this.state.projectName} onChange={(e)=>this.setState({projectName:e.target.value})} allowClear size='large' placeholder='' className='input'/>
                        </Col>
                        <Col md={8} className='input-item'>
                            <label></label>
                            <Input value={this.state.money} onChange={(e)=>this.setState({money:e.target.value})} allowClear size='large' placeholder='' className='input'/>
                        </Col>
                        <Col md={8} className='input-item'>
                            <label></label>
                            <Input value={this.state.date} onChange={(e)=>this.setState({date:e.target.value})} allowClear size='large' placeholder='' className='input'/>
                        </Col>
                    </Row>
                    <Checkbox.Group onChange={this.handleCheckbox}>
                         <Row className='cBox'>
                        <label></label>
                        <Col md={12} sm={12}className='checkBoxs'>
                            <Checkbox className='cbox' value='iOS'>iOS</Checkbox>
                            <Checkbox className='cbox' value=''></Checkbox>
                            <Checkbox className='cbox' value='Web '>Web </Checkbox>
                            <Checkbox className='cbox' value='UI/UE'>UI/UE</Checkbox>
                        </Col>
                        <Col md={12}  sm={12} className='checkBoxs'>
                            <Checkbox className='cbox' value='Android'>Android</Checkbox>
                            <Checkbox className='cbox' value=''></Checkbox>
                            <Checkbox className='cbox' value=''></Checkbox>
                        </Col>
                    </Row>
                    </Checkbox.Group>
                   
                    <Row className='cBox'>
                        <label></label>
                        <TextArea 
                        rows={6} 
                        className='textArea' 
                        placeholder=''
                        value={this.state.requireMents}
                        onChange={(e)=>this.setState({requireMents:e.target.value})}/>
                    </Row>
                </div>
                <div className='btn-box'>
                    <Button type='primary' className='btn' htmlType='submit'></Button>
                </div>
            </form>
        )
    }
    handleSubmit(e){
        console.log(this.state);
        e.preventDefault();
    }
    handleName(e){
        this.setState({
            name: e.target.value
        })
    }
    handleCheckbox(checkedValue){
        this.setState({
            projectType: checkedValue
        })
    }

}
